export default function ComicCard(props) {
  if (window.innerWidth > 768) {
    return (
      <div>
        <img src={require(`../assets/comics/${props.publisher}/${props.coverImage}.png`)} alt="cover" className=" h-full" />
      </div>
    );
  }
  else {
    return (
      <div className="flip w-full h-[250px]">
        <div className="flip-content">
          <div className="flip-front">
            <img src={require(`../assets/comics/${props.publisher}/${props.coverImage}.png`)} alt="cover" className=" h-full" />
          </div>
          <div className="flip-back">
            <div className="bg-black w-full h-full absolute opacity-100 flex items-center">

              <p className=" opacity-100 text-[8.5px] p-4 "> {props.description}</p>
            </div>
            <img src={require(`../assets/comics/${props.publisher}/${props.coverImage}.png`)} alt="cover" className=" h-full" />
          </div>
        </div>
      </div>
    );
  }
}