import news from "../assets/news/news";
import { useState, useEffect } from 'react';
import Slider from "react-slick";

export default function News() {
    const keys = ['1', '2', '3', '4', '5', '6'];
    const hrefs = [
        'https://www.exchange4media.com/digital-news/graphic-india-to-launch-first-webtoon-comics-app-toonsutra-111718.html',
        'https://variety.com/2021/digital/news/baahubali-toonsutra-comics-app-graphic-india-1234933771/',
        '',
        'https://www.globenewswire.com/en/news-release/2022/03/16/2404028/0/en/TOONSUTRA-Indian-Webtoon-Web3-Startup-Closes-Pre-Seed-Round.html',
        '',
        '',
    ]
    const [cardIndex, setCardIndex] = useState(0);
    // news['']
    if (window.innerWidth > 768) {
        return (
            <div className=" sm:my-16 mt-4 mb-16">
                <p className="sm:text-[40px] text-[17px] font-medium text-center text-[#535353] sm:my-12 my-4">News And Blogs</p>
                <div className="grid sm:grid-rows-2 sm:grid-cols-3 grid-cols-2 sm:gap-x-8 gap-x-4 gap-y-4 sm:gap-y-8 sm:w-4/5 mx-auto w-11/12">
                    {keys.map((key) =>
                        <NewsCard image={news[key]} key={key} href={hrefs[key - 1]} />
                    )}
                </div>

            </div>
        );
    } else {

        const settings = {
            className: "mx-auto w-2/3",
            centerMode: true,
            infinite: true,
            centerPadding: 0,
            slidesToShow: 1,
            speed: 500,
            autoplay: true,
            autoplaySpeed: 2000,
            // slidesToScroll: 1,
            // adaptiveHeight: true,
            beforeChange: (current, next) => {
                setCardIndex(next);
            },
        };
        const mobileKeys = ['7', '8', '9'];
        return (
            <div className=" sm:my-16 mt-4 mb-16">
                <p className="sm:text-[40px] text-[17px] font-medium text-center text-[#535353] sm:my-12 my-4">News And Blogs</p>
                <Slider {...settings}>
                    {keys.map((key) =>
                        <img src={news[key]} alt="news" className="h-96" />
                    )}
                </Slider>
            </div>
        );

    }

}

function NewsCard({ image, href }) {
    return (
        <div className="sm:h-[320px] h-32 relative">
            <a className="bg-black opacity-0 w-full h-full absolute hover:opacity-75 flex items-center justify-center" href={href} target="_blank" rel="noreferrer">
                <p className="text-white underline text-base leading-3">Read More</p>
            </a>
            <img src={image} alt="news" className="w-full h-full" />
        </div>
    );
}