export default function PreviewCard(props) {
  return (
    <div className=" shadow-2xl bg-gray-300">
      <div className=" relative z-0 w-auto">

        <div className="z-10 absolute sm:pl-[65px] sm:pr-[40px] pl-[12px] pr-[4px] flex flex-col justify-center h-full text-white">
          <p className="text-white font-semibold sm:text-[31px]">{props.title}</p>
          <p className="text-white sm:pt-[50px] text-[8.5px] sm:text-[25px]  sm:font-light">{props.description}</p>
        </div>
        <div className="w-full h-full">
          <div className="bg-black absolute w-full h-full opacity-75"></div>
          <img src={require(`../assets/comics/${props.publisher}/${props.coverImage}.png`)} alt="cover" className="h-full md:w-full" />
        </div>
      </div>
    </div>
  )
}