import ComicCard from "./ComicCard"
import { useEffect, useState } from "react";
import CSVFile from "../assets/Website-Metadata.csv";
import Papa from "papaparse";
import Slider from "react-slick";
import { Link } from "react-router-dom";

var comicList = {
    "Graphic India": [],
    "Liquid Comics": [],
    "Valiant Comics": [],
}
export default function ComicSlide() {
    const [comics, setComics] = useState([]);
    const [preview, setPreview] = useState([]);
    const [publisher, setPublisher] = useState("Graphic India");
    const [highlist, setHighlist] = useState([true, false, false]);

    const parseData = (data) => {
        for (let i = 0; i < data.length; i++) {
            // if title contains ":" then remove it
            if (data[i].TITLE.includes(":")) {
                data[i].TITLE = data[i].TITLE.replace(":", "");
            }
            else if (data[i].TITLE.includes('"')) {
                data[i].TITLE = data[i].TITLE.replace('"', "");
            }
            // console.log(data[i].TITLE);
            comicList[data[i].PUBLISHER].push({ title: data[i].TITLE, description: Object.values(data[i])[2] });
            // console.log(comicList['Graphic India'][0][0]);
            // console.log(comicList['Graphic India'][1]);
        }
        setComics(comicList);
        // preview.pop();
        // preview.pop();
        // preview.push(comicList[publisher][0][0], comicList[publisher][1]);   
        // setPreview(preview);
        // renderCards("Graphic India");
    }
    const readCSV = () => {
        fetch(CSVFile)
            .then(r => r.text())
            .then(text => {
                Papa.parse(text, {
                    header: true,
                    skipEmptyLines: true,
                    complete: function (results) {
                        // console.log(results.data);
                        parseData(results.data);
                    },
                });
            });
    }
    const pubs = ["Graphic India", "Liquid Comics", "Valiant Comics"];
    // const displayComics = JSON.parse(JSON.stringify(comicList));
    console.log(comicList["Graphic India"].length);
    const audio = new Audio("../assets/click.wav");
    const settings = {
        className: "sm:mx-8 mx-auto sm:w-2/3 w-screen",
        centerMode: true,
        infinite: true,
        centerPadding: 0,
        slidesToShow: window.innerWidth > 768 ? 5 : 3,
        speed: 500,
        autoplay: true,
        autoplaySpeed: 2000,
        nextArrow: <Arrow />,
        prevArrow: <Arrow />,
        // slidesToScroll: 1,
        // adaptiveHeight: true,
        beforeChange: (current, next) => {
            setCardIndex(next);
            // Navigator.vibrate(100);
            audio.play();

        },
    };
    useEffect(() => {
        console.log("useEffect");
        readCSV();
        setPublisher("Graphic India");
    }, []);
    const [cardIndex, setCardIndex] = useState(0);
    return (
        <>
            <div className="">
                <p className="text-center w-4/5 mx-auto sm:text-[36px] text-[17px] font-medium sm:py-4">
                    Here's a limited preview of the vast online library on Toonsutra
                </p>
            </div>
            <div className="sm:h-96 sm:my-0 my-6 flex items-center justify-center">
                <Slider {...settings}>
                    {
                        comics['Graphic India'] && comics['Graphic India'].map((comic, index) => {
                            return (
                                <div className={`transform transition-transform duration-300 w-32 h-full ${index === cardIndex ? " scale-110 opacity-100": "scale-[0.7] opacity-50" }`}>
                                    <img src={require(`../assets/comics/GraphicIndia/${comic.title.replace(/\s+/g, '')}.png`)} alt="cover" className=" h-full w-full mx-0 my-auto" />
                                </div>

                            )
                        })
                    }

                </Slider>
            </div>
            <p className="text-center sm:text-xl text-[15px] sm:py-4 sm:ml-8">
                And <b>hundreds</b> of titles like these
            </p>
            <p className="text-center sm:text-[15px] text-[8px] underline mx-auto">
                <Link to="/library"> Learn More</Link>
            </p>
            <div className="sm:block mx-auto bg-gradient-to-r to-[#DA6732] from-[#D93B47] py-4 my-4 sm:px-6 px-4 rounded-full hover:scale-105 transition-all hover:shadow-md sm:w-1/5 w-2/3 text-center">
                <p className=' text-backgroundPrimary font-medium'>Toonsutra coming soon</p>
            </div>
        </>
    )
}

function Arrow(props) {
    const { className, style, onClick } = props;
    return (
        <div
            className="w-0 h-0"
            style={{ ...style, display: "block", background: "black" }}
            onClick={onClick}
        />
    );
}